import Popup from '../../../common_ui/popup';

import UploadVideoPanel from '../../../common_ui/UploadVideoPanel';

import VideoService from '../../../services/video.service';
import ChannelInfo from '../../../common_ui/ChannelInfo';

export default (() => {
    let $popup_container = null;
    let $popup = null;

    let uiChannelInfo = null;
    let uiUploadVideoPanel = null;
    let $btn_upload_video = null;

    let $video_list = null;
    let listData = [];
    let selectedItemIndex = 0;

    let $check_live = null;
    let $check_ext = null;

    let $btn_confirm = null;
    let _confirmEvent = null;

    let pending = false;

    const init = async (onConfirm) => {
        $popup_container = $('#popup-join-video');
        $popup = $popup_container.children('.popup');

        uiChannelInfo = new ChannelInfo();
        uiChannelInfo.init('#channel-info-in-join-popup');

        uiUploadVideoPanel = new UploadVideoPanel();
        uiUploadVideoPanel.init($popup_container);
        $btn_upload_video = $('#btn-upload-video');

        $video_list = $popup.find('.video-list .list-container .list');

        $check_live = $('#check-policy-use_live');
        $check_ext = $('#check-policy-use_ext')

        $btn_confirm = $popup.find('#btn-confirm-join-video');
        _confirmEvent = onConfirm;
        // $input_file.hide();
        bindEventHandler();

    }
    
    const setChannelInfo = (userSeq) => {
        uiChannelInfo.setData(userSeq);
    }

    const reset = async () => {
        uiUploadVideoPanel.reset();
        $video_list.empty();
        $check_live.prop('checked', false);
        $check_ext.prop('checked', false);

        const resList = await getMyVideoListAndAppendList();
        bindEventListItems();
        if(resList) {
            $('.video-item').eq(0).addClass('selected')
            selectedItemIndex = 0;
        }
    }


    const getMyVideoListAndAppendList = async () => {
        const resMyVideoList = await VideoService.getVideoList(0, 10, "01", USER_SESSION.user_seq);
        const vods = resMyVideoList.data.vods;
        if(!vods) {
            alert($.i18n.t('wrng_err_n_try'));
            return false;
        }
        listData.push(...vods);
        vods.map( (vod) => {
            const thumbnail = vod.thumbnail_path;
            $video_list.append(createVideoItem(vod.vod_seq, thumbnail, vod.title));
        })
        return vods.length;
    }

    const bindEventHandler = () => {

        $('.btn-popup-close').click((e) => {
            if (pending) {
                $popup_container.hide();
                confirm(" ?",
                    () => {
                        uiUploadVideoPanel.cancel();
                    },
                    () => {
                        $popup_container.show();
                    })
            } else {
                Popup.close();
            }
        });

        $btn_upload_video.on('click', async (e) => {

            e.preventDefault();
            pending = true;
            const $btn_submit = $('#btn-upload-video');
            $btn_submit.hide();

            const resVideoInfo = await uiUploadVideoPanel.sendVideo()
            .then(res => {
                $popup_container.hide();
                pending = false;
                alert($.i18n.t("wrng_uploaded"), () => {
                    uiUploadVideoPanel.reset();
                    $btn_upload_video.show();
                    $popup_container.show();
                });
            })
            .catch(err => {
                pending = false;
                $btn_submit.show();
                alert(err.message);
                return;
            });
            // const videoSeq = resVideoInfo.data.vod.vod_seq;
            // console.log(resVideoInfo.data)
            // const thumbnail = resVideoInfo.data.vodThumbnails.domain + resVideoInfo.data.vodThumbnails.path;
            // const title = resVideoInfo.data.vod.title;
            // $video_list.prepend(createVideoItem(videoSeq, thumbnail, title));
            // data.splice(0, 0, resVideoInfo.data);

            // bindEventListItems();
        });


        $btn_confirm.click( (e) => {
            if (!listData[selectedItemIndex]) {
                $popup_container.hide();
                alert($.i18n.t('sel_vod'), () => {
                    $popup_container.show();
                });
                return;
            }

            if (!$check_live.prop('checked') || !$check_ext.prop('checked')) {
                $popup_container.hide();
                alert($.i18n.t('wrng_agree_use'),
                () => {
                    $popup_container.show();
                });
                return;
            }
            _confirmEvent(listData[selectedItemIndex])
            Popup.close();
        })
    }

    const bindEventListItems = () => {
        $('.video-item').off('click');
        $('.video-item').click( (e) => {

            $('.video-item').removeClass('selected');
            const itemIndex = $('.video-item').index($(e.currentTarget));
            const $new_item = $(e.currentTarget);
            $new_item.addClass('selected');
            
            selectedItemIndex = itemIndex;
        })
    }

    const open = async () => {
        reset();
        Popup.open($popup_container);
    }

    // url  parameter 

    function getParams(sname) {
        var params = sname.substring(sname.indexOf("?") + 1);
        var sval = "";
        params = params.split("&");
        let ret = {};
        params.forEach( param => {
            const splitParam = param.split("=");
            const objKey = splitParam[0];
            const objVal = splitParam[1];
            ret[objKey] = objVal;
        })
        return ret;
    }

    return {
        init, open, setChannelInfo
    }
})()


const createVideoItem = (videoSeq, thumbnail, title) => {
    return `
        <li class="video-item" data-video-seq="${videoSeq}">
            ${thumbnail ?
            `<div class="thumbnail-wrapper">
                    <img src="${thumbnail}">
            </div>` :
            `<div class="thumbnail-wrapper _blank">
                    <span class="fc-white">${title}</span>
            </div>`
            }

            <div class="title-container">
                <div class="title-wrapper fs-s">
                    ${title}
                </div>
            </div>
        </li>`
}