import Fetch from '../utils/Fetch';
export default (() => {

    const getLive = (liveSeq) => {
        return Fetch({
            url: `/api/v1/live/${liveSeq}`
        })
    }

    const getChannelInfo = (ownerSeq) => {
        //  
        return Fetch({
            url: `/api/v1/channel/${ownerSeq}`,
            type: 'GET'
        })

    }

    const addLiveViewCount = (liveSeq) => {
        return Fetch({
            url: `/api/v1/live/view/cnt/${liveSeq}`,
            type: 'PUT'
        });
    }

    // const getChannelCountInfo = ()

    const joinLive = (liveServiceID) => {
        return Fetch({
            url: `/api/v1/live/join`,
            type: 'POST',
            dataType: 'JSON',
            contentType: 'application/json',
            data: JSON.stringify({
                cojam_service_session_id: liveServiceID
            })
        });
    }

    const joinVideo = (channelSeq, userSeq, videoSeq) => {
        return Fetch({
            url: `/api/v1/vod/join`,
            type: 'POST',
            dataType: 'JSON',
            contentType: 'application/json',
            data: JSON.stringify({
                vod_join: {
                    channel_seq: channelSeq,
                    vod_seq: videoSeq,
                    user_seq: userSeq
                }
            })
        });

    }


    const getLikeMe = (liveSeq, userSeq) => {
        return Fetch({
          url: `/api/v1/live/like/check/user/${userSeq}/${liveSeq}`,
          type: 'GET'
        })
    }


    const like = (liveSeq, userSeq) => {
        return Fetch({
            url: `/api/v1/live/like`,
            type: 'POST',
            data: JSON.stringify({
                live_like: {
                    live_seq: liveSeq,
                    user_seq: userSeq
                }
            })
        })
    }

    const dislike = (liveSeq, userSeq) => {
        return Fetch({
            url: `/api/v1/live/dislike`,
            type: 'DELETE',
            data: JSON.stringify({
                live_like: {
                    live_seq: liveSeq,
                    user_seq: userSeq
                }
            })
        })
    }
    

    return {
        getLive,
        getChannelInfo,
        addLiveViewCount,
        joinLive,
        joinVideo,
        getLikeMe, like, dislike

    }
})()