// utils
import { getParameterByName } from '../../utils/ParameterParser';

// ui components
import Chat from '../../common_ui/chat';
import Tab from '../../common_ui/tab';
import ChannelInfo from '../../common_ui/ChannelInfo';

import uiLocalVideoPlayer from './ui_components/live_view.ui.local_video_player';
import uiRemoteVideoPlayer from './ui_components/live_view.ui.remote_video_player';
import uiLiveInfoPanel from './ui_components/live_view.ui.live_info_panel';
import JoinButton from './ui_components/live_view.ui.join_button';
import uiJoinVideoPopup from './ui_components/live_view.ui.join_video_popup';
import tabContents_JoinVideoList from './ui_components/live_view_ui.join_video_list';
import tabContents_JoinLiveList from './ui_components/live_view_ui.join_live_list';
import uiShareMenu from '../../common_ui/ShareMenu';
import ReportPopup from '../../common_ui/ReportPopup';
import uiStatsMonitor from './ui_components/live_view.ui.stats_monitor';
// controller
import {
  LiveViewController,
  P2PController as LiveViewController_P2P
} from './controller';

// services
import CommonService from '../../services/common.service';
import LiveViewService from '../../services/live_view.service';
import LiveService from '../../services/live.service';

// store
import { LIVE_INFO, CHANNEL_INFO, SERVICE_ID, USER_INFO } from './store/live_view.store';

// error
import * as ERROR from '../../error';

let liveSeq = null;
let localStream = null;
let uiJoinListPanel = null;
let uiJoinLiveButton = null;
let uiJoinVideoButton = null;
let uiChatPanel = null;
let uiChannelInfo = null;
let uiReportPopup = null;
$(document).ready(async () => {
  // init ui components
  uiLocalVideoPlayer.init('#local-video-player');
  uiRemoteVideoPlayer.init();
  uiLiveInfoPanel.init();

  uiJoinListPanel = new Tab();
  uiJoinListPanel.init('#tab-panel-join-list');

  uiChannelInfo = new ChannelInfo();
  uiChannelInfo.init('#channel-info-in-page', true);

  tabContents_JoinVideoList.init();
  tabContents_JoinLiveList.init();

  uiJoinVideoPopup.init(confirmJoinVideo);

  uiJoinLiveButton = new JoinButton(
    'join_with_live',
    'cancel_join_live',
    onClick_JoinLive,
    onClick_CancelJoinLive
  );
  uiJoinLiveButton.init('#btn-join-live');

  uiJoinVideoButton = new JoinButton(
    'join_with_video',
    'cancel_join_video',
    onClick_JoinVideo,
    onClick_CancelJoinVideo
  );

  uiJoinVideoButton.init('#btn-join-video');

  uiShareMenu.init();
  uiReportPopup = ReportPopup();
  uiReportPopup.init();
  // init events
  // uiJoinButton.attachEventHandler({
  //     joinEventHandler: joinEvent,
  //     cancelEventHandler: cancelJoinEvent
  // });
  uiStatsMonitor.init();

  uiLocalVideoPlayer.attachCloseEventHandler(onClick_CancelJoinLive);
  uiChatPanel = new Chat();
  if (USER_SESSION) {
    const chatSender = {
      avatar: USER_SESSION.avatar_url,
      name: USER_SESSION.nick_name
    };
    uiChatPanel.init('.chat-container', sendChatMessage, chatSender);
  } else {
    uiChatPanel.init('.chat-container');
  }

  // settings info ( LIVE INFO )
  let liveInfo = await getLiveInfo();
  if(!liveInfo || liveInfo.length === 0 || Object.keys(liveInfo).length === 0) {
    alert($.i18n.t("ended_live"), () => {
      history.back()
    });
    return;
  }

  LIVE_INFO.set(liveInfo);

  const resChannelInfo = await LiveViewService.getChannelInfo(LIVE_INFO.get().user_seq)
  CHANNEL_INFO.set(resChannelInfo.data.channel_info);

  const resMediaServiceID_LIVE = await CommonService.getMediaServiceID_LIVE(
    CURRENT_ID,
    LIVE_INFO.get().live_seq
  );
  const resMediaServiceID_P2P = await CommonService.getMediaServiceID_P2P(
    CURRENT_ID
  );
  const mediaServiceID_LIVE =
    resMediaServiceID_LIVE.data.cojam_service_session_id;
  const mediaServiceID_P2P =
    resMediaServiceID_P2P.data.cojam_service_session_id;
  SERVICE_ID.setMediaServiceID_LIVE(mediaServiceID_LIVE);
  SERVICE_ID.setMediaServiceID_P2P(mediaServiceID_P2P);

  // start watch
  LiveViewController.init(uiRemoteVideoPlayer);
  LiveViewController.start({
    onOpenHandler: onOpenEvent,
    onCloseHandler: onCloseEvent,
    msgFilters: [recvChatMessage],
    opFilters: [inviteFilter]
  });

  uiLiveInfoPanel.setData();
  const userInfo = await uiChannelInfo.setData(LIVE_INFO.get().user_seq, CHANNEL_INFO.get().channel_seq);
  USER_INFO.set(userInfo);
  uiJoinVideoPopup.setChannelInfo(LIVE_INFO.get().user_seq);
  tabContents_JoinLiveList.startJoinList();
  tabContents_JoinVideoList.startJoinList();
  uiShareMenu.setData(`/live/${LIVE_INFO.get().live_seq}`, CHANNEL_INFO.get().title);
  uiShareMenu.setEnable(true);
  uiLiveInfoPanel.startCountInfo();
  // uiReportPopup.setData(
  //   channelInfoDetail.profile_img_url,
  //   channelInfoDetail.nick_name,
  //   CHANNEL_INFO.get().title,
  //   "",
  //   LIVE_INFO.get().live_seq,
  //   USER_SESSION.user_seq
  // )
  bindEventHandler();
});

const bindEventHandler = () => {
  $('#btn-report').click( e => {
    uiReportPopup.open(
      USER_INFO.get().profile_img_url,
      USER_INFO.get().nick_name,
      CHANNEL_INFO.get().title,
      "",
      LIVE_INFO.get().live_seq,
      USER_SESSION.user_seq
    )
  })

  $('#input-msg').click( e => {
    e.preventDefault();
    if(!USER_SESSION) {
      location.href = "/login";
    }
  })

  $(window).bind("beforeunload", function (e) {

    onClick_CancelJoinLive();

  });

  $('#lbl-channel-title').click(function (e) {
    if (e.ctrlKey || e.metaKey) { // ctrl
      uiStatsMonitor.open();
    }
  })
}

const inviteFilter = (cmd, payload) => {
  if (cmd === 'invite') {
    const { token } = payload;
    LiveViewController_P2P.start(localStream, token, onOpen_P2P, onClose_P2P );
  }
};

const onOpen_P2P = () => {
  alert($.i18n.t("join_start"), () => {
    uiLocalVideoPlayer.setOnAirState(true);
  });
}

const onClose_P2P = () => {
  uiLocalVideoPlayer.setOnAirState(false)
}

const getLiveSeq_FromPath = () => {
  let paths = window.location.pathname.split('/');
  return paths[2];
};

const getLiveInfo = async () => {
  liveSeq = getLiveSeq_FromPath();
  let resLive = null;
  try {
    resLive = await LiveViewService.getLive(liveSeq);
    // if(resLive.data.live === null) {
    //     ERROR.notFoundLive();
    //     return false;
    // }
  } catch (err) {
    // ERROR.notFoundLive();
    // return false;
  }

  return { ...resLive.data.live };
};

const onOpenEvent = (rtcConnection) => {
  if (USER_SESSION) {
    uiJoinLiveButton.setEnable(true);
    uiJoinVideoButton.setEnable(true);
  }
  if (USER_SESSION) {
    var userSeq = USER_SESSION.user_seq;
    var liveSeq = LIVE_INFO.get().live_seq;

    LiveViewService.getLikeMe(userSeq, liveSeq).then(res => {
      if (res.data.live_like_cnt === 1) {
        uiLiveInfoPanel.likeButton.setEnable(true);
      }
    });
  }

  // clearInterval(intVal_RefreshChanneCountInfo);
  // intVal_RefreshChanneCountInfo = setInterval(() => {
  //   getChannelCountInfo();
  // }, 3000);

  // $("#msg-wait-live").hide();
  // $('#msg-end-live').hide();
  uiStatsMonitor.setRtcConnection(rtcConnection);
};

const onCloseEvent = () => {
  uiRemoteVideoPlayer.stop();
  uiJoinLiveButton.setEnable(false);
  uiJoinVideoButton.setEnable(false);
  uiLiveInfoPanel.stopCountInfo();
  uiLocalVideoPlayer.close();
  localStream = null;
};

const sendChatMessage = message => {
  var avatarUrl = USER_SESSION.avatar_url;
  var name = USER_SESSION.nick_name;
  LiveViewController.sendChatMessage(avatarUrl, name, message);
};

const recvChatMessage = recvMessage => {
  const parsedMessage = JSON.parse(recvMessage.msg);
  const { message, avatar, nickname } = parsedMessage;
  uiChatPanel.appendMessage(avatar, nickname, message);
};

const onClick_JoinVideo = () => {
  uiJoinVideoPopup.open();
};

const confirmJoinVideo = async data => {
  const channelSeq = CHANNEL_INFO.get().channel_seq;
  const userSeq = USER_SESSION.user_seq;
  let videoSeq = null;
  if (data.vod) {
    videoSeq = data.vod.vod_seq;
  } else {
    videoSeq = data.vod_seq;
  }

  LiveViewService.joinVideo(channelSeq, userSeq, videoSeq)
  .then(res => {
    const { status, message } = res;
    if (status !== 200) {
      alert(message);
      return;
    }
  })
  .catch(err => {
    console.log(err)
  });
};

const onClick_CancelJoinVideo = () => { };

const onClick_JoinLive = async () => {
  alert($.i18n.t("wrng_share_cam_to_core"), async () => {
    try {
      const userMedia = await navigator.mediaDevices.getUserMedia({
        audio: {
          echoCancellation: true,
          noiseSuppression: true
        },
        video: {
          width: 640,
          height: 480
        }
      });

      localStream = applyVoiceFilter(userMedia);

    } catch (err) {
      alert($.i18n.t("wrng_device_not_found"));
      return false;
    }

    LiveViewService.joinLive(SERVICE_ID.getMediaServiceID_LIVE())
      .then(res => {
        const { status, message } = res;
        if (status !== 200) {
          
          alert(i18next.t('dont_join_live'));
          return;
        }
        uiLocalVideoPlayer.open(localStream);
        uiJoinLiveButton.setJoinCancelMode('cancel');
      })
      .catch(err => {
        console.log(err)
      });
  });
};

const onClick_CancelJoinLive = async () => {
  await LiveService.cancelJoin(LIVE_INFO.get().live_seq, USER_SESSION.user_seq);
  localStream = null;
  uiLocalVideoPlayer.close();
  LiveViewController_P2P.stop();
  uiJoinLiveButton.setJoinCancelMode('join');
};

const applyVoiceFilter = (originStream) => {
  let tracks = [];

  // apply audio filter
  const audioCtx = new AudioContext();
  const destination = audioCtx.createMediaStreamDestination();

  const sourceNode = audioCtx.createMediaStreamSource(originStream);

  const gainNode = audioCtx.createGain();
  gainNode.gain.value = 1.0;

  const filter = audioCtx.createBiquadFilter()
  var geometricMean = Math.sqrt(300 * 3400);
  filter.frequency.value = geometricMean;
  filter.Q.value = geometricMean / (300 - 3400);

  sourceNode.connect(filter);
  filter.connect(gainNode);

  gainNode.connect(destination);

  // get audio tracks & add track
  tracks.push(...destination.stream.getTracks());

  // get video tracks & add track
  tracks.push(...originStream.getVideoTracks());

  const ret = new MediaStream(tracks);
  return ret
}