export default (() => {
    let $video_player = null;
    let $video_element = null;
    let $btn_close = null;
    let _closeEvent = null;
    let _isOpened = false;

    const init = (videoPlayerSelectQuery) => {
        $video_player = $(videoPlayerSelectQuery)
        $video_element = $video_player.find('.video-in-player');
        $btn_close = $video_player.find('#btn-close-local-player');

        $video_player.hide();
    }

    const attachCloseEventHandler = (closeEvent) => {
        _closeEvent = closeEvent;
        $btn_close.click( (e)=> {
            e.preventDefault();
            _closeEvent();
            close();
        })
    }

    const open = (stream) => {
        if(stream) {
            $video_element.get(0).srcObject = stream;
            $video_player.show();
            _isOpened = true;
        } else {
            alert($.i18n.t('wrng_device_not_found'))
        }
    }


    const close = () => {
        $video_element.get(0).srcObject = null;
        $video_player.hide();
    }

    const setOnAirState = (val) => {
        if(val) {
            $video_player.find('.onair-status').addClass('on')
        } else {
            $video_player.find('.onair-status').removeClass('on')
        }
    }

    return {
        init,
        attachCloseEventHandler,
        open,
        close,
        setOnAirState
    }
})()