export default (time) => {
    var date = new Date((time || "").replace(/-/g, "/").replace(/[TZ]/g, " ").split(".")[0]),
        diff = (((new Date()).getTime() - date.getTime()) / 1000);

    diff = diff - 33000;
    if (diff < 0) diff = 0;
    var day_diff = Math.floor(diff / 86400);

    if (isNaN(day_diff) || day_diff < 0)
        return;

    const locale = localStorage.getItem('user_language')
    if(locale === "en-US") {
        return day_diff == 0 && (
            diff < 3600 && Math.floor(diff / 60) + " min(s)" ||
            diff < 86400 && Math.floor(diff / 3600) + " hour(s)") ||
            day_diff < 7 && day_diff + " day(s)" ||
            day_diff < 31 && Math.floor(day_diff / 7) + " week(s)" ||
            day_diff < 360 && Math.floor(day_diff / 30) + " month(s)" ||
            day_diff >= 360 && (Math.floor(day_diff / 360) == 0 ? 1 : Math.floor(day_diff / 360)) + " year(s)"
    }

    return day_diff == 0 && (
        diff < 60 && " " ||
        diff < 120 && "1 " ||
        diff < 3600 && Math.floor(diff / 60) + " " ||
        diff < 7200 && "1 " ||
        diff < 86400 && Math.floor(diff / 3600) + " ") ||
        day_diff == 1 && "" ||
        day_diff < 7 && day_diff + " " ||
        day_diff < 31 && Math.floor(day_diff / 7) + " " ||
        day_diff < 360 && Math.floor(day_diff / 30) + " " ||
        day_diff >= 360 && (Math.floor(day_diff / 360) == 0 ? 1 : Math.floor(day_diff / 360)) + " "
}
