import LiveService from '../../../services/live.service';

import { LIVE_INFO } from '../store/live_view.store';

export default (()=>{
    const REFRESH_TIME = 3000;

    let $tab_contents = null;
    let $list = null;
    let $list_count = null;
    let intval_joinList = null;
    const init = () => {
        $tab_contents = $('#tab-contents-live');
        $list = $tab_contents.find('#list-join-live');
        $list_count = $tab_contents.find('.title').find('.count');
    }

    const getJoinList = async () => {
        const resGetJoinList = await LiveService.getJoinList(LIVE_INFO.get().live_seq);

        $list.find('.empty-msg').remove();
        $list.empty()
        if (!resGetJoinList ||
            !resGetJoinList.data ||
            !resGetJoinList.data.joins ||
            resGetJoinList.data.joins.length === 0) {
            $list_count.text(0);
            $list.append(`<span class='empty-msg'>${i18next.t('not_wait')}</span>`)
            return;
        }

        const list = resGetJoinList.data.joins;
        list.map( (data) => {
            const {avatar_url, nick_name} = data;
            $list.append(
                createCard(avatar_url, nick_name)
            )
        })

        $list_count.text(list.length)

    }

    const startJoinList = () => {
        getJoinList();
        intval_joinList = setInterval( () => {
            getJoinList();
        }, REFRESH_TIME);
    }

    const stopJoinList = () => {
        clearInterval(intval_joinList);
    }


    return {
        init, startJoinList, stopJoinList
    }
})()



const createCard = (avatar_url, name) => {
    return `
    <li class="join-user">
        <div class="avatar-container">
            <img src="${avatar_url}">
        </div>
        <div class="name-container bold">
            ${name}
        </div>
    </li>`
}
