export default (() => {
    let $video_player = null;
    const init = () => {
        $video_player = $('#remote-video');
    }

    const attachTrack = (stream) => {
        $video_player.show();

        $video_player.get(0).pause();
        $video_player.get(0).currentTime = 0;
        $video_player.get(0).srcObject = stream;
        $video_player.get(0).oncanplay = () => {
            $video_player.get(0).play();
        };
    }

    const stop = () => {
        $video_player.get(0).srcObject = null;
    }

    return {
        init,
        attachTrack,
        stop
    }
})()