import POPUP from "./popup";
import CommonService from '../services/common.service';

export default () => {
    let $popup_container = null;
    let $contents_info = null;
    let $type_list = null;
    let codeList = [];
    let $input_detail = null;
    let $btn_confirm = null;
    let _contentsTypeKey = "";
    let _contentsTypeList = [];
    let _contentsType = null;
    let _contentsSeq = 0;
    let _reporterSeq = 0;

    const init = () => {
        $popup_container = $('#report-popup');
        $contents_info = $('.report-contents-info');
        $type_list = $popup_container.find('#type-list');
        $input_detail = $popup_container.find('textarea');
        $btn_confirm = $popup_container.find('.btn-container').find('.btn-popup-ok');
        $popup_container.hide();

        bindEventHandler();
    }

    const open = async (profile_url, name, title, contentsCodeName, contentsSeq, reporterSeq) => {
        $contents_info.find('.avatar-container img').attr('src', profile_url);
        $contents_info.find('.name-container').text(name);
        $contents_info.find('.title').text(title)
        _contentsTypeKey = contentsCodeName;
        _contentsSeq = contentsSeq;
        _reporterSeq = reporterSeq;

        POPUP.open($popup_container);
        let resGetContentsType = null;
        if (!_contentsType) {
            resGetContentsType = await CommonService.getContentsType();
            _contentsTypeList.push(...resGetContentsType.data.Codes)
        }
        _contentsType = _contentsTypeList.find(k => k.code_name === _contentsTypeKey)

        const resGetCode = await CommonService.getCode(3);
        codeList = [];
        codeList.push(...resGetCode.data.Codes);
        $type_list.empty();
        codeList.map((codeInfo, index) => {
            $type_list.append(
                createListElement(codeInfo.code, i18next.t(`rprt_t_${codeInfo.code}`), index)
            )
        })
    }

    const bindEventHandler = () => {
        $btn_confirm.click( async (e) => {
            const reportCode = $type_list.find('input[name=report-type]:checked').attr('data-code');

            const resPostReport = await CommonService.postReport(
                _contentsType.code,
                _contentsSeq,
                _reporterSeq,
                reportCode,
                $input_detail.val()
            )
            const { status, message } = resPostReport;
            POPUP.close();
            if(status === 200) {
                alert(i18next.t('rprt_success'));
                return;
            } else {
                alert(message);
                return;
            }
            
        })
    }
    

    return {
        init, open
    }
}

const createListElement = (code, text, index) => {
    return `
    <li>
        <input type="radio" id="report-type-${index}" name="report-type" ${index === 0 ? 'checked="checked"' : ''} data-code="${code}">
        <label class="radio" for="report-type-${index}"></label>
        <label for="report-type-${index}" class="fs-xs">${text}</label>
    </li>`
}