import CommonService from '../services/common.service';
import SubscribeButton from '../common_ui/SubscribeButton';
import LiveService from '../services/live.service';
class ChannelInfo {
    constructor() {
        this.$channel_info_container = null;
        this.$profile_img = null;
        this.$user_name = null;
        this.$onair = null;
        this.$onair_text = null;
        this.$video_count = null;
        this.$btn_subscribe = null;
        this.visibleSubscribeButton = true;
        this.uiSubscribeButton = null;
        this.userSeq = 0;
    }

    init(selectQuery, visibleSubscribeButton) {
        this.$channel_info_container = $(selectQuery);
        this.$profile_img = this.$channel_info_container.find('.avatar-container img');
        this.$user_name = this.$channel_info_container.find('.name-container');
        this.$onair = this.$channel_info_container.find('.onair-status');
        this.$video_count = this.$channel_info_container.find('.video-count .count');
        this.visibleSubscribeButton = visibleSubscribeButton;
        if(!visibleSubscribeButton) return;
        this.uiSubscribeButton = new SubscribeButton();
        this.uiSubscribeButton.init(this.$channel_info_container)
    }

    async setData(userSeq, channelSeq) {
        return new Promise((resolve, reject) => {
            CommonService.getUserInfo(userSeq)
            .then(res => {
                const { live_yn, nick_name, profile_img_url, subscription_cnt, vod_cnt } = res.data;
                // live_yn: "Y"
                // nick_name: "GiMan Jeon"
                // profile_img_url: "https://dev.cojam.tv/static/cojam/res/ic-blank-avatar.png"
                // subscription_cnt: 0
                // vod_cnt: 21
                this.userSeq = userSeq;
                this.channelSeq = channelSeq;
                this.$profile_img.attr('src', profile_img_url);
                this.$user_name.text(nick_name);
                if (live_yn === "Y") {
                    this.$onair.addClass('on');
                }
                this.$video_count.text(vod_cnt);
                if (!this.uiSubscribeButton) return;
                this.uiSubscribeButton.setCount(subscription_cnt);

                if (!this.visibleSubscribeButton) return;
                this.uiSubscribeButton.setData(channelSeq)

                this.bindEventHandler();

                resolve(res.data)
            })
        })

        
    }

    bindEventHandler() {
        this.$user_name.click( (e) => {
            e.preventDefault();
            location.href = `/channel/${this.userSeq}`
        })

        $('.video-count').click( (e) => {
            e.preventDefault();
            location.href = `/channel/${this.userSeq}`

        })

        this.$onair.click( async (e) => {
            const resGetLive = await LiveService.getRecommendLive(this.userSeq, 0, 1);
            if(!resGetLive || !resGetLive.data || !resGetLive.data.live || resGetLive.data.live.length === 0) {
                alert($.i18n.t('wrng_err_n_try'));
                return;
            }
            const live_seq = resGetLive.data.live[0].live_seq;
            location.href = `/live/${live_seq}`;
        })
    }
}

export default ChannelInfo