export default (() => {
    const INTVAL_REFRESH_COUNT_INFO = 3000;
    
    let $lbl_channel_title = null;
    let $lbl_channel_desc = null;
    let $lbl_live_view_count = null;
    let $lbl_join_count = null;
    let $btn_like = null;
    let $lbl_like_count = null;
    let pendingLike = false;
    let $btn_share = null;
    let $btn_report = null;

    let intVal_CountInfo = null;

    // let intVal_RefreshChanneCountInfo
    const init = () => {
        $lbl_channel_title = $('#lbl-channel-title');
        $lbl_channel_desc = $('#lbl-channel-desc');
        $lbl_live_view_count = $('#live-count .count');
        $lbl_join_count = $('#join-count .count');
        $btn_like = $('#btn-like');
        $lbl_like_count = $btn_like.find('.count');
        $btn_share = $('#btn-share');
        $btn_report = $('#btn-report');

        bindEventHandler();
    }

    const bindEventHandler = () => {
        $btn_like.click( (e) => {
            if(!USER_SESSION) {
                location.href = "/login";
            }
            const count = parseInt($lbl_like_count.text());
            var liveSeq = LIVE_INFO.get().live_seq;
            var userSeq = USER_SESSION.user_seq;
            if($(e.currentTarget).hasClass('active')) {
                pendingLike = true;
                $(e.currentTarget).removeClass('active');
                $lbl_like_count.text(count - 1);
                LiveViewService.dislike(liveSeq, userSeq)
                    .then((res) => {
                        pendingLike = false;
                    })
                    .catch((err) => {
                        console.log(err)
                        $lbl_like_count.text(count);
                        pendingLike = false;
                    })
            } else {
                pendingLike = true;
                $(e.currentTarget).addClass('active');
                $lbl_like_count.text(count + 1);

                LiveViewService.like(liveSeq, userSeq)
                    .then((res) => {
                        pendingLike = false;
                    })
                    .catch((err) => {
                        $lbl_like_count.text(count)
                        pendingLike = false;
                    })
            }
        })
    }

    const setData = () => {
        $lbl_channel_title.text(CHANNEL_INFO.get().title)
        $lbl_channel_desc.text(CHANNEL_INFO.get().description);
        if(!USER_SESSION) return;
        getLikeInfo();
    }

    const startCountInfo = () => {
        LiveService.getChannelCountInfo(LIVE_INFO.get().live_seq)
        .then(res => {
            $lbl_live_view_count.text(res.data.live_cnt)
            $lbl_like_count.text(res.data.like_cnt)
        })
        getJoinCount();
        intVal_CountInfo = setInterval( () => {
            LiveService.getChannelCountInfo(LIVE_INFO.get().live_seq)
            .then(res => {
                $lbl_live_view_count.text(res.data.live_cnt)
            })

            getJoinCount();
        }, INTVAL_REFRESH_COUNT_INFO);
    }

    const getJoinCount = () => {
        const countElements = $('.tab-contents .count');
        const joinCount = parseInt($(countElements[0]).text()) + parseInt($(countElements[1]).text());
        $lbl_join_count.text(joinCount)
    }


    const stopCountInfo = () => {
        clearInterval(intVal_CountInfo)
    }

    const getLikeInfo = () => {
        var liveSeq = LIVE_INFO.get().live_seq;
        var userSeq = USER_SESSION.user_seq;
        LiveViewService.getLikeMe(liveSeq, userSeq)
        .then((res) => {
            if (res.data.live_like_cnt === 1) {
                $btn_like.addClass("active");
            }
        });
    }

    return {
        init,
        setData,
        startCountInfo,
        stopCountInfo
    }
})()