import { SERVICE_ID } from '../store/live_view.store';

import { produce, LIVE as LIVE_EVENT } from '../../../event';

export default (() => {
  let _callee = null;
  let IS_LIVE_JOINING = false;

  const start = (stream, token, onOpenHandler, onCloseHandler) => {
    if (_callee) _callee.stop();

    _callee = new ARTC.P2P.CALLEE();

    _callee.start({
      userId: SERVICE_ID.getMediaServiceID_P2P(),
      token,
      onTrack: event => {
        const { track, streams } = event;
        if (track.kind === 'audio') {
          // disable live audio
          _disableLiveAudio();

          // enable remote peer audio
          // _callee.audioElement = document.createElement('audio');
          // _callee.audioElement.setAttribute('autoplay', true);
          // _callee.audioElement.setAttribute('playsinline', true);
          // _callee.audioElement.setAttribute('controls', true);
          // _callee.audioElement.style.position = 'absolute';
          // _callee.audioElement.style.bottom = '40px';
          // _callee.audioElement.srcObject = streams[0];
          // document.querySelector('.video-container').append(_callee.audioElement);
        }
      },
      stream: stream,
      onOpen: stream => {
        if (stream) {
          IS_LIVE_JOINING = true;
          /// TODO: LIVE_VIEWER mute
          onOpenHandler();
        } else {
          /// datachannel connected
        }
      },
      onClose: () => {
        IS_LIVE_JOINING = false;

        // enable live audio
        _enableLiveAudio();

        /// clear remote peer audio
        // _callee.audioElement.pause();
        // _callee.audioElement.parentNode.removeChild(_callee.audioElement);
        // _callee.audioElement = null;
        _callee.stop();
        _callee = null;

        onCloseHandler();
      },
      onMessage: event => {
        // NOTE: Is this called?
        const recvMessage = event.data;
        const { message, avatar } = JSON.parse(recvMessage);

        printChatMessageLine({
          message,
          avatar,
          locationType: 'left',
          datetimeLocation: 'rignt',
          nameLocation: 'left',
          name: nickName
        });
      },
      onOperation: event => {
        const { cmd } = event;
        if (cmd === 'close') {
          IS_LIVE_JOINING = false;

          // enable live audio
          _enableLiveAudio();

          /// clear remote peer audio
          // _callee.audioElement.pause();
          // _callee.audioElement.parentNode.removeChild(_callee.audioElement);
          // _callee.audioElement = null;
          _callee.stop();
          _callee = null;
          onCloseHandler();
        }
      }
    });
  };

  const stop = () => {
    IS_LIVE_JOINING = false;

    _enableLiveAudio();

    if (_callee) {
      _callee.sendClose();
      // _callee.audioElement.pause();
      // _callee.audioElement.parentNode.removeChild(_callee.audioElement);
      // _callee.audioElement = null;
      _callee.stop();
      _callee = null;
    }
  };

  const _disableLiveAudio = () => produce(LIVE_EVENT.AUDIO.DISABLE);
  const _enableLiveAudio = () => produce(LIVE_EVENT.AUDIO.ENABLE);

  return {
    start,
    stop
  };
})();
