class JoinButton  {
    constructor(textJoin, textCancel, joinEvent, cancelEvent) {
        this.$join_button = null;
        this.enabled = false;
      
        this.joinEvent = null;
        this.cancelEvent = null;
        this.buttonText = { join: textJoin, cancel: textCancel }

        if(!this.joinEvent) {
            this.joinEvent = joinEvent;
        }

        if(!this.cancelEvent) {
            this.cancelEvent = cancelEvent;
        }

    }


    init(selectQuery) {
        this.$join_button = $(selectQuery);
        this.bindEventHandler();

        this.setJoinCancelMode("join");
        this.setEnable(false);
    };

    isActive() {
        return this.$join_button.hasClass('active');
    }

    bindEventHandler() {
        this.$join_button.click( (e) => {
            if(this.isActive()) {
                if(!this.enabled) {
                    if(!USER_SESSION) {
                        location.href = "/login";
                    } else {
                        alert($.i18n.t('msg_loading_live'));
                    }
                    return;
                }
                if(this.joinEvent) {
                    this.joinEvent();
                }    
            } else {
                if(this.cancelEvent) {
                    this.cancelEvent();
                }
            }

        });
    };

    setEnable(enabled) {
        this.enabled = enabled;
    };

    setJoinCancelMode(mode) {
        if (mode === "join" || mode === true) {
            this.$join_button.addClass("active");
            this.$join_button.find('span').text(i18next.t(this.buttonText.join))
        } else {
            this.$join_button.removeClass("active");
            this.$join_button.find('span').text(i18next.t(this.buttonText.cancel))
        }
    };
    
}

export default JoinButton