export default (() => {
    const createCard = ({title, name, avatar_url, thumbnail, url, duration = "00:00", view_cnt = 0, update, user_seq}) => {
        let displayTitle = title ? title : "TV ";
        let displayName = name ? name : "CoJamTV";
        let avatarURL = avatar_url ? avatar_url : "/static/cojam/res/ic-blank-avatar.png";
        if(thumbnail) {
            asyncImageLoader(thumbnail, $(`li[data-contents-url="${url}"]`))
        }
        return `
        <li class="card" data-contents-url="${url}">
            <div class="thumbnail-wrapper" data-contents-url="${url}">
                ${thumbnail ?
                    `<img src="${thumbnail}">`
                :
                    `<div class="blank"></div>`
                }
                <div class="view-count-info">
                    <div class="view-count-wrapper left">
                        <img src="/static/cojam/res/ic-card-view.png">
                        <span class="view-count fs-info fc-white">${view_cnt}</span>
                    </div>
                    <div class="time-wrapper right">
                        <span class="time fs-info fc-white">${duration}</span>
                    </div>
                </div>
            </div>
            <div class="title-container fs-s">
                ${displayTitle}
            </div>
            <div class="name-container" data-user-seq=${user_seq}>
                <div class="avatar-section">
                    <img src="${avatarURL}">
                </div>
                <div class="name-section fs-xs">
                    <span class="name">${displayName}</span>
                </div>
                <div class="update-section fs-xs fc-gray">
                    <span>${update}</span>
                </div>
            </div>
        </li>
        `
    }

    const createLiveCard = ({ title, name, avatar_url, thumbnail, url, live_cnt = 0, user_seq }) => {
        let displayTitle = title ? title : "TV ";
        let displayName = name ? name : "CoJamTV";
        let avatarURL = avatar_url ? avatar_url : "/static/cojam/res/ic-blank-avatar.png";
        if (thumbnail) {
            asyncImageLoader(thumbnail, $(`li[data-contents-url="${url}"]`))
        }
        return `
        <li class="card live" data-contents-url="${url}">
            <div class="thumbnail-wrapper">
                ${thumbnail ?
                `<img src="${thumbnail}">`
                :
                `<div class="blank"></div>`
            }
                <div class="view-count-info">
                    <div class="view-count-wrapper left">
                        <img src="/static/cojam/res/ic-card-signal.png">
                        <span class="view-count fs-info fc-white">${live_cnt}</span>
                    </div>
                </div>
            </div>
            <div class="title-container fs-s">
                ${displayTitle}
            </div>
            <div class="name-container" data-user-seq=${user_seq}>
                <div class="avatar-section">
                    <img src="${avatarURL}">
                </div>
                <div class="name-section fs-xs">
                    <span class="name">${displayName}</span>
                </div>
            </div>
        </li>
        `
    }

    const asyncImageLoader = (imageUrl, $targetListItemElement) => {
        let imgObj = new Image();
        let loaded = false;
        imgObj.onload = () => {
            $targetListItemElement.find('.thumbnail-wrapper > img').attr('src', imageUrl);
            $targetListItemElement.find('.thumbnail-wrapper').removeClass('loading');
            loaded = true;
        }
        imgObj.src = imageUrl;
        if(!loaded) {
            $targetListItemElement.find('.thumbnail-wrapper').addClass('loading'); 
        }
    }

    const bindEventHandlerAll = () => {
        $('.card .thumbnail-wrapper, .card .title-container').click( (e) => {
            let itemRoot = $(e.currentTarget).closest('.card');
            let url = null;
            url = itemRoot.attr('data-contents-url');
            location.href = url;
            // location.href = url;
        })

        $('.card .name-container').off('click');
        $('.card .name-container').click((e) => {
            e.preventDefault();
            const userSeq = $(e.currentTarget).attr('data-user-seq');
            location.href = `/channel/${userSeq}`;
        })
    }

    return {
        createCard, createLiveCard, bindEventHandlerAll
    }
})();