class Tab {
    constructor () {
        this.$tab_parent = null;
    }

    // init parameters
    // 1.  query
    // 2. [1   , 2   , ...]
    //     .
    //    null or undefined
    init(selectQuery, activeTabEvent) {
        if(!selectQuery) {
            console.log("** Tab **");
            console.log("  .");
            return;
        }

        this.$tab_parent = $(selectQuery);

        //     
        this.$tab_parent.find('.tab-contents').hide();
        this.$tab_parent.find('.tab-contents:first').show();

        this.$tab_parent.find('ul.tabs li').click( (e) => {
            e.preventDefault();
            this.$tab_parent.find('ul.tabs li').removeClass('active');
            $(e.target).addClass('active');

            this.$tab_parent.find('.tab-contents').hide();
            let activeTab = $(e.target).attr('rel');
            $('#' + activeTab).fadeIn();

            if (activeTabEvent.length === 0) return;
            const index = $('ul.tabs li').index($(e.currentTarget));
            if (activeTabEvent[index]) {
                activeTabEvent[index]();
            }
        })
    }

    changeTab(id) {
        $(`[rel=${id}]`).trigger('click');
    }

}

export default Tab;