import CommonService from '../../../services/common.service';
import LiveViewService from '../../../services/live_view.service';
import { LIVE_INFO, SERVICE_ID } from '../store/live_view.store';
import { listen, LIVE as LIVE_EVENT } from '../../../event';

export default (() => {
    let uiVideoPlayer = null;
    let _liveViewer = null;

    const init = videoPlayer => {
        uiVideoPlayer = videoPlayer;
        _liveViewer = new ARTC.LIVE.SUB();
    };

    const start = async ({
        onOpenHandler,
        onCloseHandler,
        msgFilters = [],
        opFilters = []
    }) => {
        try {
            _liveViewer.stop();
        } catch (e) {
            console.log(e);
        }
        _liveViewer.start({
            token: LIVE_INFO.get().room_token,
            userId: SERVICE_ID.getMediaServiceID_LIVE(),
            onTrack: event => {
                const { track, streams } = event;
                uiVideoPlayer.attachTrack(streams[0]);
            },
            onOpen: ({rtcConnection}) => {
                LiveViewService.addLiveViewCount(LIVE_INFO.get().live_seq);
                if (onOpenHandler) onOpenHandler(rtcConnection);
            },
            onMessage: message => {
                msgFilters.map(msgFilter => {
                    msgFilter(message);
                });
            },
            onError: async error => {
                console.log('** Live error:', error);

                if (error.type === 'enter_response' && error.status === 429) {
                    alert($.i18n.t("ended_live"), () => {
                        history.back();
                    });
                }
            },
            onClose: () => {
                // location.href = "/";
                console.log('');
                onCloseHandler();
            },
            onOperation: async operation => {
                if(operation === 'keep_alive') {
                    console.log('keep_alive? skip');
                    return;
                }

                const { cmd, payload } = operation;
                const { token } = payload;
                // console.group('operation received');
                // console.log(cmd);
                // console.log(payload);
                // console.log('service id: ', SERVICE_ID.getMediaServiceID_P2P());
                // console.groupEnd();
                if (opFilters) {
                    opFilters.map(opFilter => opFilter(cmd, payload));
                }
            }
        });
        console.log();
    };

    const sendChatMessage = (avatar, nickname, message) => {
        // if (_broadcaster === null) {
        //   alert(' .');
        //   return;
        // }
        _liveViewer.sendChatMessage(
            JSON.stringify({
                nickname,
                avatar,
                message
            })
        );
    };

    const _initCustomEvent = () => {
        listen(LIVE_EVENT.AUDIO.DISABLE, () => {
            _liveViewer.disableRemoteAudio();
        });
        listen(LIVE_EVENT.AUDIO.ENABLE, () => {
            _liveViewer.enableRemoteAudio();
        });
    };
    _initCustomEvent();

    return {
        init,
        start,
        sendChatMessage
    };
})();
