import LiveService from '../../../services/live.service';
import CardGenerator from '../../../common_ui/card_generator';

import { CHANNEL_INFO } from '../store/live_view.store';
import ClacDuration from '../../../utils/ClacDuration';
import PrettyDate from '../../../utils/PrettyDate';

export default (()=>{
    const REFRESH_TIME = 3000;

    let $tab_contents = null;
    let $list = null;
    let $list_count = null;
    let intval_joinList = null;
    const init = () => {
        $tab_contents = $('#tab-contents-video');
        $list = $tab_contents.find('#list-join-video');
        $list_count = $tab_contents.find('.title').find('.count');
    }

    const getJoinList = async () => {
        const resGetJoinList = await LiveService.getJoinList_Video(
            CHANNEL_INFO.get().channel_seq, 0, 20
        );

        $list.find('.empty-msg').remove();
        $list.empty()
        if (!resGetJoinList ||
            !resGetJoinList.data ||
            !resGetJoinList.data.vod_joins ||
            resGetJoinList.data.vod_joins.length === 0) {
            $list_count.text(0);
            $list.append(`<span class='empty-msg'>${i18next.t('not_wait')}</span>`)
            return;
        }
        const list = resGetJoinList.data.vod_joins;
        list.map( (data) => {
            const {
                vod_seq,
                nick_name,
                thumbnail_path,
                profile_img_url,
                title,
                created_at,
                duration,
                view_cnt
            } = data;
            $list.append(
                CardGenerator.createCard({
                    title,
                    name: nick_name,
                    avatar_url: profile_img_url,
                    thumbnail: thumbnail_path,
                    duration: ClacDuration(duration ? duration : 0),
                    view_cnt,
                    update: PrettyDate(created_at),
                    url: `/video/${vod_seq}`
                })
            )
        })

        $list_count.text(resGetJoinList.data.cnt)
        CardGenerator.bindEventHandlerAll();
    }

    const startJoinList = () => {
        getJoinList();
        intval_joinList = setInterval( () => {
            getJoinList();
        }, REFRESH_TIME);
    }

    const stopJoinList = () => {
        clearInterval(intval_joinList);
    }

    return {
        init, startJoinList, stopJoinList
    }
})()