import CommonService from "../services/common.service";

class SubscribeButton {
    constructor() {
        this.$btn_subscribe = null;
        this.$lbl_subscribe_count = null;
        this.defaultValue = null;
        this.channelSeq = null;
    }

    init($container) {
        this.$btn_subscribe = $container.find('.btn-subscribe');
        this.$lbl_subscribe_count = $container.find('.subscribe-count').find('.count');

        this.bindEventHandler();
    }
    
    setChannelSeq(channelSeq) {
        this.channelSeq = channelSeq
    }

    setData(channelSeq) {
        this.channelSeq = channelSeq;
        if(!USER_SESSION) return;
        CommonService.getSubscribeInfo(channelSeq)
        .then(res => {
            if(res.data.channel_subscription) {
                this.$btn_subscribe.removeClass('gray');
                this.$btn_subscribe.addClass('on');
                this.$btn_subscribe.find('span').text(i18next.t('btn_subscribed'));
                // channel_seq: 25
                // created_at: "2019-05-24T05:49:53Z"
                // updated_at: "2019-05-24T05:49:53Z"
                // user_seq: 25
            } else {
                this.$btn_subscribe.find('span').text(i18next.t('btn_subscribe'));
            }
        })
        .catch(err => {

        })
    }

    setCount(count) {
        this.$lbl_subscribe_count.text(count);
    }


    bindEventHandler() {

        this.$btn_subscribe.click( (e) => {
            if(!USER_SESSION) {
                location.href = "/login"
            }
            const count = parseInt(this.$lbl_subscribe_count.text());
            if($(e.currentTarget).hasClass('on')) {
                CommonService.unsubscribe(parseInt(this.channelSeq))
                .then(res => {
                    $(e.currentTarget).addClass('gray');
                    $(e.currentTarget).removeClass('on');
                    $(e.currentTarget).find('span').text(i18next.t('btn_subscribe'));
                    this.$lbl_subscribe_count.text(count - 1);
                })
                .catch(err => {
                })
            } else {
                CommonService.subscribe(parseInt(this.channelSeq)) 
                .then( res => {
                    $(e.currentTarget).removeClass('gray');
                    $(e.currentTarget).addClass('on');
                    $(e.currentTarget).find('span').text(i18next.t('btn_subscribed'));
                    this.$lbl_subscribe_count.text(count + 1);
                })
                .catch( err => {
                })
            }
        })
    }
}

export default SubscribeButton;