import Fetch, { FileUpload } from '../utils/Fetch';

export default (() => {
    const getMediaServiceID_LIVE = (sessionID, liveSeq) => {
        return Fetch({
            url: `/api/v1/common/cojam/serviceid/MEDIA/LIVE/${liveSeq}/${sessionID}`,
            type: 'GET'
        })
    }

    const getMediaServiceID_P2P = (sessionID) => {
        return Fetch({
            url: `/api/v1/common/cojam/serviceid/MEDIA/P2P/0/${sessionID}`,
            type: 'GET'
        })
    }

    const getUserInfo = (userSeq) => {
        return Fetch({
            url: `/api/v1/channel/vod/info/${userSeq}`
        })
    }

    const getMySubscribeList = (start, count) => {
        return Fetch({
            url: `/api/v1/channel/subscription/userlist/${count}/${start}`
        })
    }

    const getSubscribeInfo = (channelSeq) => {
        return Fetch({
            url: `/api/v1/channel/subscription/${channelSeq}`
        })
    }

    const subscribe = (channelSeq) => {
        return Fetch({
            url: `/api/v1/channel/subscription`,
            type: 'POST',
            data: JSON.stringify({
                channel_seq: channelSeq
            })
        })
    }

    const unsubscribe = (channelSeq) => {
        return Fetch({
            url: `/api/v1/channel/subscription/${channelSeq}`,
            type: 'DELETE'
        })
    }

    const getCode = (groupSeq) => {
        return Fetch({
            url: `/api/v1/common/code/${groupSeq}`
        })
    }

    const getContentsType = () => {
        return Fetch({
            url: `/api/v1/common/code/4`
        })
    }

    const postContact = (code, question) => {
        return Fetch({
            url: `/api/v1/custcenter/faq`,
            type: 'POST',
            data: JSON.stringify({
                faq_code: code,
                question
            })
        })
    }


    const postReport = (contentsType, contentsSeq, reporterSeq, reportCode, detail) => {
        return Fetch({
            url: `/api/v1/custcenter/illegality`,
            type: 'POST',
            data: JSON.stringify({
                contents_type: contentsType.toString(),
                contents_seq: contentsSeq.toString(),
                reporter: reporterSeq,
                report_code: reportCode,
                report_detail: detail
            })
        })
    }

    return {
        getMediaServiceID_LIVE,
        getMediaServiceID_P2P,
        getUserInfo,
        getMySubscribeList, getSubscribeInfo, subscribe, unsubscribe,
        getCode, getContentsType, postContact, postReport
    }
})()

