import VideoService from '../services/video.service';
const MAX_READ_COUNT_TRANSCODE_STATE = 6;
const INTVAL_READ_TRANSCODE = 10000;
const MAX_FILE_SIZE = 1024 * 1024 * 100;
class UploadVideoPanel {
    constructor() {
        this.$popup_container = null;
        this.$view_select_video = null;
        this.$btn_select_file = null;
        this.$input_file = null;
        this.$img_video_thumbnail = null;

        this.$input_title = null;
        this.$input_desc = null;

        this.$form_upload_video = null;

        this.$loading = null;

        this.resUploadVideo = null;
    }

    init($popupContainer) {
        this.$popup_container = $popupContainer;
        this.$form_upload_video = this.$popup_container.find('#form-upload-video');
        
        this.$view_select_video = this.$popup_container.find('.view-select-video');
        this.$btn_select_file = this.$view_select_video.find('#btn-select-file');
        this.$input_file = this.$view_select_video.find('#input-file');
        this.$img_video_thumbnail = this.$view_select_video.find('.img-video-thumbnail');
        this.$img_video_thumbnail.hide();
        this.$input_title = this.$popup_container.find('.input-video-title');
        this.$input_desc = this.$popup_container.find('.input-video-desc');
        
        this.$loading = this.$popup_container.find('.upload-loading');
        this.$loading.hide();
        this.bindEventHandler();
    }

    bindEventHandler() {
        const loadVideo = video =>
            new Promise((resolve, reject) => {
                video.addEventListener('loadeddata', evt => resolve());
                video.addEventListener('error', evt => reject());
                video.load();
            });

        const snapImage = video => {
            const canvas = document.createElement('canvas');
            canvas.width = video.videoWidth;
            canvas.height = video.videoHeight;
            canvas.getContext('2d').drawImage(video, 0, 0, canvas.width, canvas.height);
            const image = canvas.toDataURL();
            return image;
        };

        const handleFileLoad = async (event, fileType) => {
            const blob = new Blob([event.target.result], {
                type: fileType
            });
            const url = URL.createObjectURL(blob);

            const video = document.createElement('video');
            video.preload = 'metadata';
            video.src = url;
            video.muted = true;
            video.playsInline = true;
            // video.currentTime = 5;
            await loadVideo(video);
            // video.currentTime = 5;

            const imageData = snapImage(video);
            // NOTE: stop video loading for error

            this.$img_video_thumbnail.attr('src', imageData);
            URL.revokeObjectURL(url);
            video.pause();
        };

        this.$input_file.change(async (e) => {
            e.stopPropagation();
            const selectFile = this.$input_file.get()[0].files[0];
            if(selectFile.size > MAX_FILE_SIZE) {
                if (this.$popup_container) {
                    this.$popup_container.hide();
                }
                alert($.i18n.t("wrng_exced_limit"), () => {
                    this.$input_file.val('');
                    if (this.$popup_container) {
                        this.$popup_container.show();
                    }
                })
                return;
            }
            if (selectFile) {
                const fileReader = new FileReader();
                fileReader.onload = event => {
                    handleFileLoad(event, selectFile.type);
                };
                fileReader.readAsArrayBuffer(selectFile);
                this.$img_video_thumbnail.show();
                this.$img_video_thumbnail.css({
                    width: '265px',
                    height: '150px'
                });
                this.$btn_select_file.hide();
                this.$img_video_thumbnail.parent().show();
            } else {
                this.$img_video_thumbnail.hide();
                this.$btn_select_file.show();
            }
        })

    }

    setTitle(title) {
        this.$input_title.val(title)
    }
    getTitle() {
        return this.$input_title.val()
    }

    setDescription(desc) {
        this.$input_desc.val(desc ? desc : "")
    }
    getDescription() {
        return this.$input_desc.val()
    }

    setThumbnail(imgUrl) {
        this.$img_video_thumbnail.attr('src', imgUrl)
        this.$img_video_thumbnail.show();
    }

    disableInput() {
        this.$input_title.attr('disabled', true);
        this.$input_desc.attr('disabled', true);
        this.$input_file.attr('disabled', true);
    }

    sendVideo() {
        return new Promise(async (resolve, reject) => {
            if(!this.$input_file.get()[0].files[0]) {
                reject({ message: i18next.t('file_not_selected') });
                return;
            }
            if(this.$input_title.val().length === 0) {
                reject({ message: i18next.t('wrng_enter_title')});
                return;
            }
            
            const selectedFile = this.$input_file.get()[0].files[0];
            
            const title = this.$input_title.val();
            const desc = this.$input_desc.val();
            
            if(title.length === 0) {
                return reject({ message: "  " })
            }
            if(title.length > 30) {
                return reject({ message: " 30   " });
            }
            if(desc.length > 500) {
                return reject({ message: " 500   " });
            }
            this.$loading.show();

            this.disableInput();

            const resGetUrl = await VideoService.getUploadUrl(
                USER_SESSION.user_seq,
                title,
                desc,
                selectedFile.name,
                selectedFile.size
            );

            const uploadUrl = resGetUrl.data.info.url;
            this.$loading.find('#lbl-loading-status').text(i18next.t('uploading'));
            this.resUploadVideo = VideoService.uploadVideo(
                uploadUrl,
                selectedFile,
                (progress) => {
                    let realProgress = parseInt(progress * 100);
                    this.$loading.find('#lbl-loading-progress').text(`${realProgress}%`);
                }
            )
            .then(res => {
                resolve(res)
            })
            .catch(err => {
                reject(err)
            });

            // this.$loading.find('#lbl-loading-status').text(" ...");

            // let readCount = 0;
            // let resVideoInfo = null;
            // let intVal_checkTranscoding = setInterval(async () => {
            //     resVideoInfo = await VideoService.getVideoInfo(uploadKey);
            //     console.log(resVideoInfo)
            //     readCount++;
            //     if (resVideoInfo.data.vod_path && resVideoInfo.data.vod_path.length !== 0) {
            //         this.$loading.find('img').css('animation', 'none');
            //         this.$loading.find('#lbl-loading-progress').text("100%");
            //         this.$loading.find('#lbl-loading-status').text(" ");
            //         clearInterval(intVal_checkTranscoding);
            //         setTimeout( () => {
            //             // this.$loading.hide();
            //             return resolve(resVideoInfo);
            //         }, 1000)
            //     }
            //     if (readCount === MAX_READ_COUNT_TRANSCODE_STATE) {
            //         clearInterval(intVal_checkTranscoding);
            //         this.$loading.find('#lbl-loading-status').html(" .<br />     ");
            //         // this.$loading.hide();
            //         return reject(false)
            //     }
            // }, INTVAL_READ_TRANSCODE)
        })
    }
    cancel() {
        VideoService.cancelUploadVideo();
        // this.resUploadVideo.abort();
    }
    reset() {
        this.$input_file.val('');
        this.$input_file.attr('disabled', false);
        this.$btn_select_file.show();
        this.$img_video_thumbnail.hide();
        this.$input_title.val('');
        this.$input_desc.val('');
        this.$input_title.attr('disabled', false);
        this.$input_desc.attr('disabled', false);
        this.$loading.hide();
    }

}

export default UploadVideoPanel;